import { FunctionComponent, VNode } from 'preact';
import { HTMLAttributes, useRef, useState } from 'preact/compat';
import { VComponent, classes, getFormValues } from '@adobe/elsie/lib';
import { Text, useText } from '@adobe/elsie/i18n';
import {
  Accordion,
  AccordionSection,
  Divider,
  Skeleton,
  SkeletonRow,
} from '@adobe/elsie/components';

import { ChevronDown, ChevronUp } from '@adobe/elsie/icons';

import '@adobe/elsie/components/PriceSummary/PriceSummary.css';

export interface PriceSummaryProps
  extends Omit<HTMLAttributes<HTMLDivElement>, 'loading'> {
  heading: string;
  loading?: boolean;
  total?: {
    price: VNode<HTMLAttributes<HTMLSpanElement>>;
    estimated?: boolean;
    priceWithoutTax?: VNode<HTMLAttributes<HTMLSpanElement>>;
  };
  subTotal?: {
    price: VNode<HTMLAttributes<HTMLSpanElement>>;
    priceExcludingTax?: VNode<HTMLAttributes<HTMLSpanElement>>;
    taxIncluded?: boolean;
    taxExcluded?: boolean;
    zeroTaxSubtotal?: boolean;
  };
  shipping?: {
    price: VNode<HTMLAttributes<HTMLSpanElement>>;
    estimated?: boolean;
    taxIncluded?: boolean;
    taxExcluded?: boolean;
    priceIncludingTax?: VNode<HTMLAttributes<HTMLSpanElement>>;
    priceExcludingTax?: VNode<HTMLAttributes<HTMLSpanElement>>;
    countryField?: VNode<HTMLAttributes<HTMLInputElement>>;
    stateField?: VNode<HTMLAttributes<HTMLInputElement>>;
    zipField?: VNode<HTMLAttributes<HTMLInputElement>>;
    destinationText?: string;
    onEstimate?: (formData: any) => void;
    estimateButton?: VNode<HTMLAttributes<HTMLButtonElement>>;
  };
  taxTotal?: {
    price: VNode<HTMLAttributes<HTMLSpanElement>>;
    estimated?: boolean;
  };
  taxesApplied?: {
    label: string;
    price: VNode<HTMLAttributes<HTMLSpanElement>>;
  }[];
  discounts?: {
    label: string;
    price: VNode<HTMLAttributes<HTMLSpanElement>>;
    caption?: VNode<HTMLAttributes<HTMLDivElement>>;
  }[];
  primaryAction?: VNode<HTMLAttributes<HTMLButtonElement>>;
}

export const PriceSummary: FunctionComponent<PriceSummaryProps> = ({
  className,
  children,
  ...props
}) => {
  const {
    heading,
    loading = true,
    total,
    subTotal,
    shipping,
    taxTotal,
    taxesApplied,
    discounts,
    primaryAction,
    ...restProps
  } = props;

  const [showTaxBreakdown, setShowTaxBreakdown] = useState(false);

  const estimateFormRef = useRef<HTMLFormElement>(null);
  const [showEstimateForm, setShowEstimateForm] = useState(true);
  const [estimateField, setEstimateField] = useState<'zip' | 'state'>('zip');

  const translations = useText({
    editZipAction: 'Dropin.PriceSummary.shipping.editZipAction',
    destinationLinkAriaLabel:
      'Dropin.PriceSummary.shipping.destinationLinkAriaLabel',
    taxesBreakdownTitle: 'Dropin.PriceSummary.taxes.breakdown',
    subtotalLabel: 'Dropin.PriceSummary.subTotal.label',
    shippingLabel: 'Dropin.PriceSummary.shipping.label',
    showTaxBreakdown: 'Dropin.PriceSummary.taxes.showBreakdown',
    hideTaxBreakdown: 'Dropin.PriceSummary.taxes.hideBreakdown',
    zipPlaceholder: 'Dropin.PriceSummary.shipping.zipPlaceholder',
  });

  const onDestinationLinkClick = (e: Event) => {
    e.preventDefault();
    setShowEstimateForm((show) => !show);
  };

  const onAlternateFieldClick = (e: Event) => {
    e.preventDefault();
    setShowEstimateForm(true);

    setEstimateField((field) => (field === 'zip' ? 'state' : 'zip'));
  };

  const onSubmitEstimate = (e: Event) => {
    e.preventDefault();
    setShowEstimateForm(false);

    const formData = getFormValues(estimateFormRef.current!);

    shipping?.onEstimate?.(formData);
  };

  // Skeleton content to show while loading
  const skeletonContent = (
    <Skeleton data-testid="loading-skeleton" rowGap="xsmall">
      <SkeletonRow
        variant="row"
        size="small"
        lines={5}
        fullWidth={true}
        multilineGap="xsmall"
      />
    </Skeleton>
  );

  // Entry for the shipping price
  const shippingContent = shipping && (
    <>
      <div
        data-testid="shipping-content"
        className={classes([
          'dropin-price-summary__entry',
          'dropin-price-summary__shipping',
        ])}
      >
        <span className={'dropin-price-summary__label'}>
          {shipping.estimated ? (
            shipping.destinationText ? (
              <>
                <Text id="Dropin.PriceSummary.shipping.estimatedDestination" />
                <a
                  className={'dropin-price-summary__shippingLink'}
                  role="button"
                  href=""
                  onClick={onDestinationLinkClick}
                  onKeyDown={(event) => {
                    if (event.key === 'Enter' || event.key === ' ') {
                      onDestinationLinkClick(event);
                    }
                  }}
                  tabIndex={0}
                  aria-label={translations.destinationLinkAriaLabel}
                  data-testid="shipping-destination-link"
                >
                  {shipping.destinationText}
                </a>
              </>
            ) : (
              <Text id="Dropin.PriceSummary.shipping.estimated" />
            )
          ) : (
            <Text id="Dropin.PriceSummary.shipping.label" />
          )}
        </span>
        <VComponent
          node={shipping.price}
          className={'dropin-price-summary__price'}
        />

        {shipping.estimated && (
          <>
            <div className={classes(['dropin-price-summary__caption'])}>
              <a
                href="#"
                className={'dropin-price-summary__shippingLink'}
                onClick={onAlternateFieldClick}
                data-testid="shipping-alternate-field-link"
              >
                {estimateField === 'zip' ? (
                  <Text id="Dropin.PriceSummary.shipping.alternateField.state" />
                ) : (
                  <Text id="Dropin.PriceSummary.shipping.alternateField.zip" />
                )}
              </a>
            </div>
          </>
        )}

        {/* Edit shipping address form */}
        {shipping.estimated && (
          <>
            <form
              className={classes([
                'dropin-price-summary__shipping--edit',
                ['dropin-price-summary__shipping--hide', !showEstimateForm],
              ])}
              ref={estimateFormRef}
              data-testid="shipping-estimate-form"
            >
              {shipping.countryField && (
                <VComponent
                  node={shipping.countryField}
                  className={classes([
                    'dropin-price-summary__shipping--country',
                  ])}
                />
              )}
              {estimateField === 'state'
                ? shipping.stateField && (
                    <VComponent
                      node={shipping.stateField}
                      className={classes([
                        'dropin-price-summary__shipping--state',
                      ])}
                    />
                  )
                : shipping.zipField && (
                    <VComponent
                      node={shipping.zipField}
                      className={classes([
                        'dropin-price-summary__shipping--zip',
                      ])}
                    />
                  )}
              {shipping.estimateButton && (
                <VComponent
                  node={shipping.estimateButton}
                  className={classes([
                    'dropin-price-summary__shipping--action',
                  ])}
                  onClick={onSubmitEstimate}
                  type="submit"
                />
              )}
            </form>
          </>
        )}

        {shipping.taxIncluded && (
          <div
            data-testid="shipping-tax-included"
            className={classes(['dropin-price-summary__caption'])}
          >
            <span>
              <Text id="Dropin.PriceSummary.shipping.withTaxes" />
            </span>
          </div>
        )}

        {shipping.taxExcluded ? (
          <div
            data-testid="shipping-tax-included-excluded"
            className={classes(['dropin-price-summary__caption'])}
          >
            <span>
              {shipping.priceExcludingTax}{' '}
              <Text id="Dropin.PriceSummary.shipping.withoutTaxes" />
            </span>
          </div>
        ) : undefined}
      </div>
    </>
  );

  // Entry for the discounts
  const discountContent = discounts && (
    <>
      {discounts.map((discount) => (
        <div
          className={classes([
            'dropin-price-summary__entry',
            'dropin-price-summary__discount',
          ])}
          key={discount.label}
        >
          <span className={classes(['dropin-price-summary__label'])}>
            {discount.label}
          </span>
          <VComponent
            node={discount.price}
            className={classes(['dropin-price-summary__price'])}
          />
          {discount.caption && (
            <VComponent
              node={discount.caption}
              className={classes(['dropin-price-summary__caption'])}
            />
          )}
        </div>
      ))}
    </>
  );

  // Entry for the total tax and breakdown (if provided)
  const taxContent = taxesApplied ? (
    /* Show breakdown if provided */
    <>
      <Accordion
        data-testid="tax-breakdown"
        className={'dropin-price-summary__taxes'}
        iconOpen={ChevronDown}
        iconClose={ChevronUp}
      >
        <AccordionSection
          title={translations.taxesBreakdownTitle}
          secondaryText={
            !showTaxBreakdown && taxTotal ? (
              <VComponent
                node={taxTotal.price}
                className={'dropin-price-summary__price'}
              />
            ) : undefined
          }
          renderContentWhenClosed={false}
          onStateChange={setShowTaxBreakdown}
        >
          <div className={classes(['dropin-price-summary__appliedTaxes'])}>
            {taxesApplied.map((tax) => (
              <div
                className={classes([
                  'dropin-price-summary__entry',
                  'dropin-price-summary__taxEntry',
                ])}
                key={tax.label}
              >
                <span
                  className={classes([
                    'dropin-price-summary__label',
                    'dropin-price-summary__label--muted',
                  ])}
                >
                  {tax.label}
                </span>

                <VComponent
                  node={tax.price}
                  className={classes([
                    'dropin-price-summary__price',
                    'dropin-price-summary__price--muted',
                  ])}
                />
              </div>
            ))}
          </div>

          <div className={classes(['dropin-price-summary__entry'])}>
            <span className={'dropin-price-summary__label'}>
              <Text id="Dropin.PriceSummary.taxes.total" />
            </span>
            {taxTotal && (
              <VComponent
                node={taxTotal.price}
                className={'dropin-price-summary__price'}
              />
            )}
          </div>
        </AccordionSection>
      </Accordion>
    </>
  ) : (
    taxTotal && (
      /* Otherwise, show total only */
      <div
        data-testid="tax-total-only"
        className={classes([
          'dropin-price-summary__entry',
          'dropin-price-summary__taxTotal',
        ])}
      >
        <span className={'dropin-price-summary__label'}>
          {taxTotal.estimated ? (
            <Text id="Dropin.PriceSummary.taxes.estimated" />
          ) : (
            <Text id="Dropin.PriceSummary.taxes.totalOnly" />
          )}
        </span>
        <VComponent
          node={taxTotal.price}
          className={'dropin-price-summary__price'}
        />
      </div>
    )
  );

  // Entry for the total
  const totalContent = total && (
    <>
      <div
        data-testid="total-content"
        className={classes([
          'dropin-price-summary__entry',
          'dropin-price-summary__total',
          total.estimated && 'dropin-price-summary__total--padded',
        ])}
      >
        <span
          className={classes([
            'dropin-price-summary__label',
            'dropin-price-summary__label--bold',
          ])}
        >
          {total.estimated ? (
            <Text id="Dropin.PriceSummary.total.estimated" />
          ) : (
            <Text id="Dropin.PriceSummary.total.label" />
          )}
        </span>
        <VComponent
          node={total.price}
          className={classes([
            'dropin-price-summary__price',
            'dropin-price-summary__price--bold',
          ])}
        />
      </div>
    </>
  );

  // Entry for the total without tax
  const totalWithoutTaxContent = total && total.priceWithoutTax && (
    <div
      data-testid="total-without-tax"
      className={
        'dropin-price-summary__entry dropin-price-summary__totalWithoutTax'
      }
    >
      <span
        className={classes([
          'dropin-price-summary__label',
          'dropin-price-summary__label--muted',
        ])}
      >
        <Text id="Dropin.PriceSummary.total.withoutTax" />
      </span>
      <VComponent
        node={total.priceWithoutTax}
        className={classes([
          'dropin-price-summary__price',
          'dropin-price-summary__price--muted',
        ])}
      />
    </div>
  );

  // Entry for the primary action
  const primaryActionContent = primaryAction && (
    <div
      className={classes([
        'dropin-price-summary__entry',
        'dropin-price-summary__primaryAction',
      ])}
    >
      {primaryAction}
    </div>
  );

  const subTotalContent = subTotal && (
    <div
      className={classes([
        'dropin-price-summary__entry',
        'dropin-price-summary__subTotal',
      ])}
    >
      <span className={'dropin-price-summary__label'}>
        <Text id="Dropin.PriceSummary.subTotal.label" />
      </span>
      <VComponent
        node={subTotal.price}
        className={'dropin-price-summary__price'}
      />
      {subTotal.taxIncluded && (
        <div
          data-testid="sub-total-tax-caption"
          className={classes(['dropin-price-summary__caption'])}
        >
          <span>
            <Text id="Dropin.PriceSummary.subTotal.withTaxes" />
          </span>
        </div>
      )}

      {subTotal.taxExcluded ? (
        <div
          data-testid="sub-total-tax-caption-excluded"
          className={classes(['dropin-price-summary__caption'])}
        >
          <span>
            {subTotal.priceExcludingTax}
            &nbsp;
            <Text id="Dropin.PriceSummary.subTotal.withoutTaxes" />
          </span>
        </div>
      ) : undefined}
    </div>
  );
  return (
    <div
      {...restProps}
      className={classes(['dropin-price-summary', className])}
    >
      <div className={'dropin-price-summary__heading'}>{heading}</div>

      <Divider
        variant="primary"
        className={'dropin-price-summary__divider-primary'}
      />

      {loading ? (
        skeletonContent
      ) : (
        <>
          {subTotalContent}

          {shippingContent}

          {discountContent}

          {taxContent}

          {totalContent}

          {totalWithoutTaxContent}

          {primaryActionContent}
        </>
      )}
    </div>
  );
};
